﻿import { Component, OnInit } from "@angular/core";
import { dashboardService } from '../Services/dashboardService';
import { GoogleColumnChartService } from '../Services/google-column-chart.service';
@Component({
    selector: "dashboard",
    templateUrl: "dashboard.component.html",
    styles: []
})
export class Dashboard implements OnInit {
    title = 'Dashboard';
    public dashboard = [];
    ngOnInit(): void {
        var vertical = "fs";
        this.data.loadCustomerSense(vertical).subscribe(success => {
            if (success) {
                this.dashboard = this.data.dashboard;
                this._columnChartService.BuildColumnChart("fs", this.data.dashboard); 
            }
        });
        this.data.loadCustomerSense("ce").subscribe(success => {
            if (success) {
                this.dashboard = this.data.dashboard;
                this._columnChartService.BuildColumnChart("ce", this.data.dashboard);
            }
        });
        this.data.loadCustomerSense("ls").subscribe(success => {
            if (success) {
                this.dashboard = this.data.dashboard;
                this._columnChartService.BuildColumnChart("ls", this.data.dashboard);
            }
        });
        this.data.loadCustomerSense("bo").subscribe(success => {
            if (success) {
                this.dashboard = this.data.dashboard;
                this._columnChartService.BuildColumnChart("bo", this.data.dashboard);
            }
        });
       
    }
    constructor(private data: dashboardService, private _columnChartService: GoogleColumnChartService) { }
}