import { GoogleChartsBaseService } from './google-charts.base.service';
import { Injectable } from '@angular/core';
declare var google: any;
@Injectable()
export class GoogleColumnChartService extends GoogleChartsBaseService {
    constructor() { super(); }
    public BuildColumnChart(elementId: String, data: any[]): void {
        var chartFunc = () => { return new google.visualization.ColumnChart(document.getElementById(<string>elementId)); };
        var options = {
            legend: { position: 'top', maxLines: 3, textStyle: { color: 'black', fontSize: 16 } },
            isStacked: true,
            colors: ['#FF0000', '#ffbf00', '#32CD32'],
            vAxis: {
                minValue: 0,
                ticks: [0, 3, 6, 9]
            }
        };
        this.buildChart(data, chartFunc, options);
    }
}