import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { AppComponent } from './app.component';
import { Dashboard } from './Dashboard/dashboard.component';
import { customersense } from './CustomerSenese/customersense.component';
import { dashboardService } from './Services/dashboardService';
import { customersenseService } from './Services/customersense';
import { GoogleChartsBaseService } from './Services/google-charts.base.service';
import { GoogleColumnChartService } from './Services/google-column-chart.service';
@NgModule({
    declarations: [
        AppComponent,
        Dashboard,
        customersense
    ],
    imports: [
        BrowserModule,
        HttpClientModule
    ],
    providers: [dashboardService, GoogleChartsBaseService, GoogleColumnChartService, customersenseService],
    bootstrap: [AppComponent]
})
export class AppModule { }
