﻿import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';

@Injectable()
export class customersenseService {
    constructor(private http: HttpClient) { }

    public customersensedetails = [];

    loadCustomerSenseDetail(vertical, month, color) {
        return this.http.get("http://10.11.200.40:2121/api/Feedbackdetail?vertical=" + vertical + "&week=" + month + "&color=" + color)
            .pipe(map((data: any[]) => {
                this.customersensedetails = data;
                return true;
            }));
    }
}