﻿import { Component, OnInit } from "@angular/core";
import { customersenseService } from '../Services/customersense';
@Component({
    selector: "customersense",
    templateUrl: "customersense.component.html",
    styles: []
})

export class customersense implements OnInit {
    public customersensedata = [];
    ngOnInit(): void {
        this.data.loadCustomerSenseDetail("ce", "May 2019", "Green").subscribe(success => {
            if (success) {
                this.customersensedata = this.data.customersensedetails;
            }
        });
    }
    constructor(private data: customersenseService) { }
}